.cardMakerItem {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 1rem;
}
.line1 {
  display: flex;
  width: 100%;
  border-bottom: 1px solid lightgrey;
}
.name {
  font-size: 1.2rem;
  width: 33%;
  padding: 0.7em;
  border-radius: 20px 0 0 0;
  border-style: none;
  border-right: 1px solid lightgrey;
}
.company {
  font-size: 1.2rem;
  width: 34%;
  padding: 0.7em;
  border-style: none;
  border-right: 1px solid lightgrey;
}
.theme {
  font-size: 1.2rem;
  width: 33%;
  padding: 0.7em;
  border-radius: 0 20px 0 0;

  border-style: none;
}
.line2 {
  display: flex;
  width: 100%;
  border-bottom: 1px solid lightgrey;
}
.title {
  font-size: 1.2rem;
  width: 50%;
  padding: 0.7em;
  border-style: none;
  border-right: 1px solid lightgrey;
}
.email {
  font-size: 1.2rem;
  width: 50%;
  padding: 0.7em;

  border-style: none;
}
.line3 {
  width: 100%;
  border-bottom: 1px solid lightgrey;
}
.message {
  font-size: 1.2rem;
  width: 100%;
  padding: 0.7em;
  border-style: none;
  border-bottom: 1px solid lightgrey;
}
.line4 {
  display: flex;
  width: 100%;
}
.fileBtn {
  font-size: 1.2rem;
  width: 50%;
  padding: 0.7em;
  border-style: none;
  border-radius: 0 0 0 20px;
  border-right: 1px solid lightgrey;
  background-color: #e0ebff;
  cursor: pointer;
}
.deleteBtn {
  font-size: 1.2rem;
  width: 50%;
  padding: 0.7em;
  border-style: none;
  border-radius: 0 0 20px 0;
  background-color: #d2d2d2;
  color: white;
  cursor: pointer;
}
.deleteBtn:hover {
  opacity: 0.8;
}
